import React from 'react'
import Link from 'gatsby-link'
import Container from "../layouts/container";
import Header from "../components/header";

const links = [`Home`, `Work`, `CV`];

const Contact = (props) => (
  <Container>
    <h1> Contact </h1>
    <Header links={links} />
    <div>
      <p>I'm currently available for web dev/design work.</p>
      <p>You can reach me at <a href="mailto:rawaidakhtar@gmail.com">rawaidakhtar@gmail.com</a>!</p>
      <p> Alternatively, you can find me on <a href="https://www.linkedin.com/in/rawaid-akhtar-a5505989/"> LinkedIn </a>.</p>

      <p> Hope to hear from ya! </p>

    </div>
  </Container>
);


export default Contact;


